import React, { useState } from 'react';
import Resume from './Links/Resume';

function ProfHide(props) {
    const [resume, setResume] = useState("none");

    function Show() {
        setResume("block")
     }

  return (
    <div className="prof" style={props.style}>
      <ul style={{ display: resume === "block" ? "none" : "block" }}>
        <li onClick={Show} style={{color: 'lightgreen'}}>Resume</li>
        <li>Projects</li>
        <li>About</li>
        <li>Contact</li>
      </ul>
    
    <Resume resume={resume} setResume={setResume}/>
    
    </div>
  );
}

export default ProfHide;
